	module.exports = {"data":{"markdownRemark":{"html":"<h1>Typescript</h1>\n<h2>React</h2>\n<p><a href=\"https://github.com/sw-yx/react-typescript-cheatsheet\">React typescript cheatsheet</a></p>\n<p>Была такая проблема - я установил пакет <strong>npm</strong>, написанный на <strong>typescript</strong>, и при компиляции мой проект все время ругался на то, что не может найти <code>@types/react,</code> хотя он был установлен и в корневом проекте и в зависимости.</p>\n<p>Помогло прописать следующее в <code>tsconfig.json</code>:</p>\n<pre><code>\"compilerOptions\" : {\n    \"baseUrl\": \"./\",\n    \"paths\": {\n      \"*\": [\"node_modules/@types/*\", \"*\"]\n  }\n</code></pre>","frontmatter":{"path":"/blog/typescript","title":"Typescript"}}},"pathContext":{}}

